/**
 * HeaderComponent
 */
import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { UserService } from '../../../services/user/user.service';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
/**
 * 
 * @class HeaderComponent
 * @implements OnInit
 */
export class HeaderComponent implements OnInit {

    public page: {
        url: string;
        title: string;
        prev: boolean;
    };
    public isMenuOpen: boolean;

    constructor(
        public user: UserService,
        private router: Router
    ) { }

    /**
     * 
     * @method ngOnInit
     * @returns {Promise<void>}
     */
    public ngOnInit(): void {
        this.isMenuOpen = false;
        this.changePage(this.router.url);
        this.router.events.subscribe((event) => {
            if (event instanceof NavigationEnd) {
                this.changePage(event.url);
            }
        });
    }

    /**
     * 
     * @method menuOpen
     */
    public menuOpen(): void {
        this.isMenuOpen = true;
    }

    /**
     * 
     * @method menuClose
     */
    public menuClose(): void {
        this.isMenuOpen = false;
    }

    /**
     * 
     * @method changePage
     * @param {string} url
     */
    private changePage(url: string): void {
        const page = pages.find((value) => {
            return (value.url === url);
        });
        if (page === undefined) {
            this.page = { url: '', title: 'NOT FOUND', prev: false };

            return;
        }
        this.page = page;
    }
}

/**
 * 
 * @const pages
 */
const pages = [
    { url: '/ticket', title: '', prev: false },
    { url: '/purchase', title: '', prev: false },
    { url: '/member/point', title: '', prev: false },
    { url: '/about', title: '', prev: true },
    { url: '/policy', title: '', prev: true },
    { url: '/law', title: '', prev: true },
    { url: '/privacy', title: '', prev: true },
    { url: '/law', title: '', prev: true },
    { url: '/auth/register', title: '', prev: true },
    { url: '/auth/signOut', title: '', prev: true },
    { url: '/member/benefits', title: '', prev: true },
    { url: '/member/edit', title: '', prev: true },
    { url: '/member/withdraw', title: '', prev: true }

];



